import React from "react";
import { useStaticQuery, graphql } from "gatsby";

import Layout from "../components/Layout";

const IndexPage = props => {
  const detail = useStaticQuery(graphql`
    query {
      contentfulPortfolio(id: { eq: "688603ff-8f0a-5881-a990-8cb3ffd2c7e1" }) {
        id
        contentful_id
        portfolioTitle
        portfolioAssets {
          file {
            fileName
            url
          }
        }
        portfolioDescription {
          portfolioDescription
        }
      }
    }
  `);

  return (
    <Layout>
      <article id="main">
        <header>
          <h2>Detail</h2>
        </header>
        <section className="wrapper style5">
          <div className="inner"></div>
        </section>
      </article>
    </Layout>
  );
};

export default IndexPage;
